<template>
      <div class="historical">
        <div class="tit">{{title}}</div>
        <div class="content active">
          <div class="writediv changebac"></div>
          <ul class="cul">
            <li class="text_n " id="jv">{{conten.acc}}</li>
            <li class="text_n" id="sc">{{conten.his}}</li>
          </ul>
        </div>
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
      </div>
</template>

<style scoped="historical">
    .historical{
      width: 300px;
      height: 720px;
      background: rgba(168,151,124,1);
      border-top: 1px solid rgba(168,151,124,1);
      border-right: 1px rgba(255,255,255,0.5) solid;
    }
    .tit{
      font-size: 28px;
      text-align: center;
      color: white;
      margin-top: 30px;
    }
    .content{
      width: 120px;
      height: 500px;
      top: 75px;
      left: 85px;
      background: -webkit-gradient(linear, 0% 50%, 50% 100%, from(rgba(255,255,255,0.01)), to(rgba(255,255,255,0.6)));
      /* Webkit: Safari 5.1+, Chrome 10+ */
      background: -webkit-linear-gradient(to bottom, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.6) 50%,rgba(255,255,255,0.01) 100%);
      /* Firefox 3.6+ */
      background: -moz-linear-gradient(to bottom, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.6) 50%,rgba(255,255,255,0.01) 100%);
      /* Opera 11.10+ */
      background: -o-linear-gradient(to bottom, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.6) 50%,rgba(255,255,255,0.01) 100%);
      /* IE 10 */
      background: -ms-linear-gradient(to bottom, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.6) 50%,rgba(255,255,255,0.01) 100%);

      background: linear-gradient(to bottom, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.6) 50%,rgba(255,255,255,0.01) 100%);
      position: relative;
    }
    .content ul{
      width: 120px;
      position: absolute;
      left: 0;
      top: 210px;
    }
    .content ul .text_n{
      color: white;
      width: 120px;
      height: 70px;
      font-size: 26px;
      text-align: center;
      line-height: 70px;
    }
    .content .writediv{
      width: 120px;
      height: 70px;
      background: rgba(255,255,255,0.6);
      position: absolute;
      left:0;
      top: 210px;
    }
    .content .changebac{
      background: #A36363;
      border: 4px solid #fff;
      box-sizing: border-box;
    }
    .content.active{
      background: rgba(255,255,255,0.4);
    }
</style>

<script>
  export default {
    props:['type'],
    data() {
            return {
                title:'记录与收藏',
                num:0,
                name:"Historical",
                isShow:true,
                conten:
                  {
                    his:'记录',
                    acc:'收藏'
                  }
            }
        },
    mounted(){
      console.log(this.type+"类型");
      if(this.type!=undefined){
        if(this.type =="history"){
          this.num=1;
          $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'});
          this.$emit('pressLeft', this.isShow,this.name,"down");
        }else {
          this.num=0;
          $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'});
          this.$emit('pressLeft', this.isShow,this.name,"up");
        }
      }else {
        let types = localStorage.getItem('types');
        console.log(types);
        if (types =='fiv'){
          this.num=0;
          $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'});
          this.$emit('pressLeft', this.isShow,this.name,"up");
        }else {
          this.num=1;
          $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'});
          this.$emit('pressLeft', this.isShow,this.name,"down");
        }
      }
    },
    activated(){
      this.$nextTick(function () {
        if(this.type!=undefined){
          if(this.type =="history"){
            this.num=1;
            $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'});
            this.$emit('pressLeft', this.isShow,this.name,"down");
          }else {
            this.num=0;
            $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'});
            this.$emit('pressLeft', this.isShow,this.name,"up");
          }
        }
      });
    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        }else if(kc=="KeyEnter"){
          this.KeyEnter();
        }
      },
      center(pos){
        this.isShow = true;
        if(pos=='fav'){
//          this.$emit('pressLeft', this.isShow,this.name,"up");
          this.num-=1;
          if(this.num<=0){
            this.num=0;
          }
        }else {
//          this.$emit('pressLeft', this.isShow,this.name,"down");
          this.num++;
          if(this.num>=2){
            this.num=1;
          };
        }
        $('.writediv').addClass("changebac");
        $('.content').addClass("active");

      },
      back() {
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          this.$router.go(-1);
        }
      },
      right(){

//        console.log(this.num);

        if (this.num ==0){
          let fav = JSON.parse(localStorage.getItem('PersonMovies'));
          console.log(fav);
          if(fav.length>0){
            this.isShow= false;
            $('.writediv').removeClass("changebac");
            $('.content').removeClass("active");
            this.$emit('pressLeft', this.isShow,this.name,"right1");
          }
        }else {
          let his = JSON.parse(localStorage.getItem('historyMovies'));
          if (his.length>0){
            this.isShow= false;
            $('.writediv').removeClass("changebac");
            $('.content').removeClass("active");
            this.$emit('pressLeft', this.isShow,this.name,"right2");
          }
        }

      },
      down() {
        this.$emit('pressLeft', this.isShow,this.name,"down");
        this.num++;
        if(this.num>=2){
          this.num=1;
        };
        $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'})

      },
      up(){
        this.$emit('pressLeft', this.isShow,this.name,"up");
        this.num-=1;
        if(this.num<=0){
          this.num=0;
        };
        $('.cul').css({'transition':'all 0.3s','transform':'translateY(-'+ 70*this.num +'px)'})
//        console.log(this.num);
      },
      updateStast(bl,pos,isEnd){
        this.isShow = bl;
        this.pos = pos;
        $('.writediv').addClass("changebac");
        $('.content').addClass("active");
      }
    },
        components: {

      }
    }
</script>



